.db-left-side-bar {
  height: 100%;
  width: 250px;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.db-left-side-bar--header {
  height: 110px;
  background: var(--blue-1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.db-left-side-bar--header img {
  -ms-flex-item-align: center;
      align-self: center;
  height: 72px;
  width: 72px;
}

.db-left-side-bar--content {
  -ms-flex: 1 1;
      flex: 1 1;
  background: -webkit-gradient(linear, left top, left bottom, from(var(--blue-2)), to(var(--blue-3)));
  background: -webkit-linear-gradient(top, var(--blue-2), var(--blue-3));
  background: -o-linear-gradient(top, var(--blue-2), var(--blue-3));
  background: linear-gradient(180deg, var(--blue-2), var(--blue-3))
}

.db-left-side-bar--footer {
  height: 75px;
  background: var(--blue-4)
}

.db-nav-link {
  display: -ms-flexbox;
  display: flex;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 200;
  letter-spacing: 1.5px;
  height: 70px;
  padding-left: 45px;
  cursor: pointer;
}

.db-nav-link img, .db-nav-link--label {
  -ms-flex-item-align: center;
      align-self: center;
}

.db-nav-link img {
  margin-right: 15px;
}

.db-nav-link--label {
  color: var(--lavandar-1);
  text-decoration: none;
  padding-top: 2px;
}

.db-nav-link--label.active {
  color: var(--neon-1);
}



